@import "./src/common/styles/sass/baseColors";
@import "./src/common/styles/CommonStyles";

.main {
  padding: 80px 0 100px;
  margin: 0 auto;
  width: 90%;
  min-height: 75%;
}




.mainContainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .main {
    justify-content: space-around;
  }
}

.loadMoreButton {
  display: flex;
  justify-content: center;
}