.modal {
  height: 100vh;
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.4);
  z-index: 10;
  //color: #2d2e46
}

.wrapper {
  width: 400px;
  background: #f9f9fe;
  border-radius: 8px;
}

.wrap {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 250px;
  padding: 22px 24px 30px;

}